function getStorageKey(key: string) {
    return "bond-finder-" + key;
}

function load(key: string, json = false) {
    try {
        const value = localStorage.getItem(getStorageKey(key));
        return json ? JSON.parse(value) : value;
    } catch {
        return null;
    }
}

function save(key: string, value: any, json = false) {
    try {
        localStorage.setItem(
            getStorageKey(key),
            (json ? JSON.stringify : String)(value)
        );
    } catch {
        // mute
    }
}

export default { load, save };
