import storageUtil from "./storageUtil";

const STORAGE_KEY = "settings-v1";

export interface ISettings {
    rub: boolean;
    min_month: string;
    min_year: string;
    month: string;
    year: string;
    sector1: boolean;
    sector2: boolean;
    sector3: boolean;
    highRisk: boolean;
    maxListLevel: string;
    maxPerYear: string;
    requiredCouponMonth: string;
    couponType: string;
    maxAmort: string;
    minTotalTradesPerc: string;
}

const TODAY = new Date();
const TODAY_PLUS_1Y = new Date(TODAY.getFullYear() + 1, TODAY.getMonth(), 1);
const TODAY_PLUS_4Y = new Date(TODAY.getFullYear() + 4, TODAY.getMonth(), 1);

function getMonthText(date: Date): string {
    const m = 1 + date.getMonth();
    return m > 9 ? String(m) : ("0" + m);
}

const DEFAULT_SETTINGS: ISettings = {
    rub: true,
    min_month: getMonthText(TODAY_PLUS_1Y),
    month: getMonthText(TODAY_PLUS_4Y),
    min_year: String(TODAY_PLUS_1Y.getFullYear()),
    year: String(TODAY_PLUS_4Y.getFullYear()),
    sector1: true,
    sector2: true,
    sector3: true,
    highRisk: true,
    maxListLevel: "2",
    maxPerYear: "4",
    requiredCouponMonth: "0",
    couponType: "0",
    maxAmort: "90",
    minTotalTradesPerc: "50"
};

export function load(): ISettings {
    const result = { ...DEFAULT_SETTINGS };
    const storedSettings = storageUtil.load(STORAGE_KEY, true);

    if (storedSettings && typeof storedSettings === "object") {
        for (const key of Object.keys(DEFAULT_SETTINGS)) {
            if (key in storedSettings) {
                result[key] = storedSettings[key];
            }
        }
    }

    return result;
}

export function save(settings: ISettings) {
    storageUtil.save(STORAGE_KEY, settings, true);
}
