import Vue from "vue";
import IReportRow from "./report-row";
import { hasOffer, isVarCoupon } from "./report-row-extensions";

const PILL_BADGE_CLASS_PREFIX = "badge badge-pill badge-";

interface IContext {
    row: IReportRow;
}

Vue.component("bond-row", {
    props: [ "row" ],
    template: "#bond-row-template",
    computed: {

        moexUrl(this: IContext) {
            return "https://www.moex.com/ru/issue.aspx?code=" + this.row.isin;
        },

        sectorBadgeClass(this: IContext) {
            switch (this.row.sector) {
                case 1:
                case 2:
                    return PILL_BADGE_CLASS_PREFIX + "success";
                case 3:
                    return PILL_BADGE_CLASS_PREFIX + "warning";
            }
        },

        sectorName(this: IContext) {
            switch (this.row.sector) {
                case 1: return "ОФЗ";
                case 2: return "Муни";
                case 3: return "Корп";
            }
        },

        listLevelBadgeClass(this: IContext) {
            const row = this.row;
            switch (row.listLevel) {
                case 1: return PILL_BADGE_CLASS_PREFIX + "success";
                case 2: return PILL_BADGE_CLASS_PREFIX + "warning";
                case 3: return row.isHighRisk
                    ? PILL_BADGE_CLASS_PREFIX + "dark bond-finder-ir-badge"
                    : PILL_BADGE_CLASS_PREFIX + "danger";
            }
        },

        listLevelName(this: IContext) {
            const row = this.row;
            switch (row.listLevel) {
                case 1: return "I";
                case 2: return "II";
                case 3: return row.isHighRisk
                    ? "\xa0"
                    : "III";
            }
        },

        showCouponStar(this: IContext) {
            return isVarCoupon(this.row)
                || hasOffer(this.row);
        },

        rowClass(this: IContext) {
            return this.row.infoUrl ? "bond-finder-separator" : null;
        }
    }
});
