import storageUtil from "./storageUtil";

const KEY = "night";

let currentMode: number;
let wasScrolled = false;

function prefersDark() {
    try {
        return window.matchMedia && matchMedia("(prefers-color-scheme: dark)").matches;
    } catch {
        return false;
    }
}

function loadMode() {
    const loadResult = storageUtil.load(KEY);
    if (loadResult == null) {
        return -1;
    }
    return Number(loadResult);
}

function getMode() {
    return currentMode;
}

function setMode(mode: number) {
    currentMode = mode;
    storageUtil.save(KEY, mode);
    updateMarkup();
}

function isNight() {
    if (currentMode < 0) {
        return prefersDark();
    }
    return currentMode > 0;
}

function toggleClass(element: HTMLElement, className: string, active: boolean) {
    const classList = element.classList;
    if (active) {
        classList.add(className);
    } else {
        classList.remove(className);
    }
}

function updateMarkup() {
    toggleClass(document.documentElement, "night", isNight());
}

function onDocScroll() {
    const scrolled = window.pageYOffset > 0;
    if (wasScrolled !== scrolled) {
        toggleClass(document.documentElement, "scrolled", scrolled);
        wasScrolled = scrolled;
    }
}

currentMode = loadMode();

document.addEventListener("scroll", onDocScroll, false);

export default { getMode, setMode, updateMarkup };
